<template>
  <div class="hello">
    <h1>Hike patrullen</h1>
    
    <GmapMap :center="center" :zoom="11" map-type-id="terrain" style="width: 100%; flex-grow:1">
      <gmap-info-window :options="infoOptions" :position="infoWindowPos" :opened="infoWinOpen" @closeclick="infoWinOpen=false">
        {{infoContent}}
      </gmap-info-window>

 
      <GmapMarker :key="index" v-for="(m, index) in markers" :position="m" :clickable="true"  @click="toggleInfoWindow(m,index)"/>
    </GmapMap>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      markers: [],
      center: {
        lat: 46.579427,
        lng: 13.968488
      },
      infoContent: "",
      infoWindowPos: null,
      infoWinOpen: false,
      currentMidx: null,
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35
        }
      }
    };
  },

  created() {
    console.log("created");
    this.$options.sockets.onmessage = data => {
      console.log("marker event");
      this.markers = JSON.parse(data.data);
    };
    window
      .fetch("http://localhost:3000/hikers")
      .then(r => r.json())
      .then(data => {
        console.log(data);
        this.markers = data;
        this.msg = JSON.stringify(data);
      });
  },
  methods: {
    toggleInfoWindow: function(marker, idx) {
      this.infoWindowPos = marker;
      this.infoContent = marker.name || marker.id;

      //check if its the same marker that was selected if yes toggle
      if (this.currentMidx === idx) {
        this.infoWinOpen = !this.infoWinOpen;
      } else {
        //if different marker set infowindow to open and reset current marker index
        this.infoWinOpen = true;
        this.currentMidx = idx;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
.hello {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 16px);
}
</style>



